      nombre: "Enganches y cuerdas",
      significado:
        "<p>Enganches y cuerdas energéticas</p><p>Son otra forma por medio de la cual nuestro entorno cercano interactúa con nosotros a nivel energético. Puede desestabilizar nuestro bienestar, pues es tan común como las formas de pensamiento negativo e incluso están asociados; cuando existe uno, existe el otro, con el mismo patrón energético.</p><p>Generalmente, se da con personas con las que pasamos bastante tiempo, aquellas que forman parte de nuestro núcleo cercano. Debido al tipo de relación que tenemos con ellos, proyectan mentalmente lo que se visualiza como un cordón o enganche que conecta directamente uno o varios de nuestro chakras con uno de ellos.</p><p>Generalmente son inconscientes y se producen cuando proyectamos en otra persona una emoción, sensación o percepción con la suficiente intensidad como para que se quede enganchada a la otra persona y se cree una cadena permanente. Son conexiones que absorben parte de la energía del chakra en el cual tenemos el enganche, causando una disfunción y, normalmente, un infrafuncionamiento.</p><p>La diferencia entre enganches y cordones reside en la intrusibidad del tipo de conexión. Los enganches generan un programa: son como un virus que, además de absorber parte de la energía del chakra, genera cierto tipo de emociones o sensaciones en la persona que las recibe. Terminan, en la mayoría de los casos, siendo formas de pensamiento negativas en el aura del receptor.</p><p>Las cuerdas no generan o transmiten estos programas o virus externos y nos hacen perder energía; pueden causar desarmonía y desbalances en nuestra aura y cuerpos sutiles.</p>",
