      nombre: "Conexiones compasivas",
      significado:
        "<p>Conexiones compasivas</p><p>Cuando un alma ofrece sus recursos energéticos a otra para asistirla y ayudarla en su crecimiento y evolución, se crean lo que llamamos conexiones compasivas: permitimos a otras personas que se conecten a nuestro sistema energético, mientras les proporcionamos lo que necesitan o parte de ello, para su propio camino y evolución.</p><p>Lo que hacemos es volvernos sus baterías y funcionar como suplemento energético de aquellos a los que queremos, pero, evidentemente, esto nos desgasta y reduce la energía disponible para nuestro camino.</p><p>Hay que entender que la energía del Universo es infinita para todos y que debemos  dejar que todos se conecten y se nutran de ella, y no ofrecer nuestros  recursos para ello. Si aparecen en una lectura, debemos averiguar con quién las tenemos, por qué, desde cuándo y  limpiarlas sin ningún atisbo de culpa; pues, no es de nosotros de quien se debe alimentar el aura de los demás, a pesar de que nos parezca un hecho bondadoso o noble.</p>",
